<template>

    <div class="agreement-signing">

        <div class="agreement-signing-title">Ваши данные</div>

        <div class="data-list">
            <div class="data-list-item">
                <div class="h6">Фамилия</div>
                <p>{{esiaData.lastName}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Имя</div>
                <p>{{esiaData.firstName}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Отчество</div>
                <p>{{esiaData.middleName}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Снилс</div>
                <p>{{esiaData.snils | VMask('###-###-### ##')}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">ИНН</div>
                <p>{{esiaData.inn}}</p>
            </div>
        </div>

        <div class="h5">Паспортные данные</div>

        <div class="data-list">
            <div class="data-list-item">
                <div class="h6">Серия паспорта</div>
                <p>{{esiaData.passport_series}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Номер</div>
                <p>{{esiaData.passport_number}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Кем выдан</div>
                <p>{{esiaData.passport_issuedBy}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Дата выдачи</div>
                <p>{{esiaData.passport_issueDate}}</p>
            </div>
            <div class="data-list-item">
                <div class="h6">Код</div>
                <p>{{esiaData.passport_issueId}}</p>
            </div>
        </div>

        <div class="agreement-signing-info">
            <div class="h5"
                 v-html="$t('agreement.signing_title')"/>
            <p v-html="$t('agreement.signing_descr')"/>
            <a
                href="#"
                class="link link-with-loading"
                :class="{'loading': downloadLoading}"
                v-html="$t('agreement.signing_link_text')"
                @click.prevent="loadDocuments"/>
        </div>

        <div class="agreement-signing-btns">
            <button class="btn btn-block btn-sm"
                    @click="agreementConfirm">Подписать
            </button>
            <a href="#"
               class="decline-link"
               @click.prevent="declineModal = true">Отклонить</a>
        </div>

        <Popup v-if="declineModal"
               ref="declineModal"
               @close-modal="declineModal = false"
               :classes="'reject-modal-confirm'">

            <div class="popup-title">
                <div class="h3">Вы уверены, что хотите <br>отклонить получение кредита?</div>
            </div>

            <div class="reject-confirm-btns">
                <button @click="$refs.declineModal.closePopup()"
                        class="btn btn-lg btn-secondary">Нет</button>
                <button @click="declineSuccess"
                        class="btn btn-lg">Да, уверен</button>
            </div>

        </Popup>

    </div>

</template>

<script>
    import {mapState} from "vuex";
    import Popup from '@/components/Blocks/Popup'
    import moment from 'moment'

    export default {
        name: "AgreementSigning",
        components: {
            Popup
        },
        computed: {
            ...mapState({
                esiaData: state => state.agreement.esiaData,
                agreementDocs: state => state.agreement.agreementDocs,
                person: state => state.confirmation.person
            })
        },
        data: () => ({
            declineModal: false,
            getParametr: '',
            code: '',
            downloadLoading: false
        }),
        methods: {
            agreementConfirm() {
                this.$store.dispatch('getSubscribeCode', this.getParametr).then(() => {
                    this.$router.push(`/agreement/confirmation/?c=${this.getParametr}`)
                })
            },
            declineSuccess() {
                this.$store.dispatch('declineAgreement', this.getParametr).then(() => {
                    this.$router.push('/agreement/result')
                })
            },
            loadDocuments () {
                let promises = []
                this.downloadLoading = true
                this.agreementDocs.forEach(doc => {
                    doc.files.forEach(file => {
                        this.$store.dispatch('getFile', file.id).then(blob => {
                            promises.push(this.$store.dispatch('downloadFile', {content: blob, name: file.name, format: file.format}))
                        })
                    })
                })
                Promise.all(promises)
                    .then(() => this.downloadLoading = false)
                    .catch(() => this.downloadLoading = false)
            }
        },
        mounted() {
            const params = window
                .location
                .search
                .replace('?','')
                .split('&')
                .reduce(
                    function(p,e){
                        const a = e.split('=');
                        p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                        return p;
                    },
                    {}
                );
            this.getParametr = params['c']
            this.code = params['code']
            this.$store.dispatch('getPassportData', {token: this.getParametr, code: this.code})
        }
    }
</script>

